.container {
  max-width: 1200px;
  margin: 50px auto;
  padding: 150px 0;
  padding-bottom: 100px;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25), -5px -5px 30px 7px rgba(0, 0, 0, 0.22);
  cursor: pointer;
  transition: 0.4s;
  background-color: #000;
  .intro {
    display: flex;
    justify-content: center;
    .intro-text {
      width: 650px;
      .label-hello {
        font-size: 20px;
        color: #ffbd39;
      }
      .your-name {
        margin: 20px 0;
        height: 150px;
        max-width: 400px;
        font-size: 60px;
        color: #fff;
        font-weight: 800;
        .typing-custom {
          display: inline;
          .title-name {
            color: #ffbd39;
            display: inline;
          }
        }
      }
      .career {
        font-size: 60px;
        color: #fff;
        font-weight: 800;
      }
    }
    .intro-img {
      .slick-slider {
        width: 400px;
        .slick-arrow {
          display: none !important;
        }
      }
    }
  }
  .about {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    .intro-img {
      img {
        width: 400px;
        margin-right: 100px;
      }
    }
    .about-info {
      width: 600px;
      .about-me {
        font-size: 60px;
        margin-bottom: 40px;
        color: #fff;
        text-shadow: 10px 0px 24px #ffbd39;

        text-align: center;
      }
      .item-info {
        display: flex;
        margin-bottom: 25px;
        .title {
          width: 180px;
          display: flex;
          align-items: center;
          i {
            width: 30px;
          }
        }
        .content {
          color: #ffbd39;
        }
      }
    }
  }
  .resume {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 50px;
    .about-info {
      .about-me {
        font-size: 60px;
        margin-bottom: 40px;
        color: #fff;
        text-shadow: 10px 0px 24px #ffbd39;
        margin-top: 40px;
        text-align: center;
      }
    }
  }
}

.wrapper {
  display: flex;
  justify-content: space-around;
}

.card {
  width: 280px;
  height: 360px;
  border-radius: 15px;
  padding: 1.5rem;
  background: white;
  position: relative;
  display: flex;
  align-items: flex-end;
  transition: 0.4s ease-out;
  box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
  margin: 0 20px;
}
.card:hover {
  transform: translateY(20px);
}
.card:hover:before {
  opacity: 1;
}
.card:hover .info {
  opacity: 1;
  transform: translateY(0px);
}
.card:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}
.card img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
}
.card .info {
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
}
.card .info h1 {
  margin: 0px;
}
.card .info p {
  letter-spacing: 1px;
  font-size: 15px;
  margin-top: 8px;
}
.card .info button {
  padding: 0.6rem;
  outline: none;
  border: none;
  border-radius: 3px;
  background: white;
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: 0.4s ease;
}
.card .info button:hover {
  background: dodgerblue;
  color: white;
}
